import { ADD_NOTE, DELETE_NOTE, SHOW_NOTE, SAVE_NOTE, SHOW_LAYER } from '../constants'

const initialState = {
  notes: [
    {
      id: 1,
      title: 'Javascript',
      content: 'JavaScriptJavaScriptHTMLHTML',
      isActive: true
  },
    {
      id: 2,
      title: 'React',
      content: 'React  JAVASCRIPT ReactUI React  MVC  Veact  Facebook  Instagram  2013  5 React ',
      isActive: false
    }
  ],
  cnote: {},
  isShowLayer: false
}

const note = (state={}, action) => {
  switch(action.type) {
    case ADD_NOTE:
      return Object.assign({}, state, {
        id: action.id,
        title: action.title,
        content: action.content
      })
    case SAVE_NOTE:
      if (action.title === state.title || action.content === state.content) return state
      return Object.assign({}, state, {
        id: action.id,
        title: action.title,
        content: action.content
        
      })
    default:
      return state
  }
}

const notes = (state=[], action) => {
  switch(action.type) {
    case ADD_NOTE:
      return [...state, note({}, action)]
    case SAVE_NOTE:
      return [...state, note({}, action)]
    default:
      return state
  }
}

const noteApp = (state=initialState, action) => {
  switch(action.type) {
    case ADD_NOTE:
      return Object.assign({}, state, {
        notes: notes(state.notes, action)
      })
    case SHOW_NOTE:
      // active
      const _notes = state.notes.map((item) => {
        if (item.id === action.id) {
          item.isActive = true
        } else {
          item.isActive = false
        }
        return item
      })

      return Object.assign({}, state, {
        notes: _notes,
        cnote: state.notes.filter(item => item.id === action.id)[0]
      })
    case DELETE_NOTE:
      return state.map(item => item.id !== action.id)
    case SAVE_NOTE:
      return Object.assign({}, state, {
        notes: notes(state.notes, action)
      })
    case SHOW_LAYER:
      return Object.assign({}, state, {
        isShowLayer: action.isShowLayer
      })
    default:
      return state
  }
}

export default noteApp




